import type {
  BridgeEvents,
  BridgeMessage,
  BridgeRequests,
  EventMessage,
  MessageHandler,
  RequestHandler,
  RequestMessage,
  ResponseMessage,
} from '@/types/bridge';
import { guid } from '@/utils';

interface PendingRequest<K extends keyof BridgeRequests> {
  resolve: (value: BridgeRequests[K]['response']) => void;
  reject: (err: Error) => void;
  timeout: ReturnType<typeof setTimeout>;
}

type EventKey = keyof BridgeEvents;
type RequestKey = keyof BridgeRequests;
const RESPONSE_TIMEOUT_MS = 10000;

export class MessageBus {
  private static instance: MessageBus;

  private eventListeners = new Map<EventKey, Set<(message: BridgeMessage) => void>>();
  private requestHandlers = new Map<
    RequestKey,
    (
      request: BridgeRequests[RequestKey]['request']
    ) => BridgeRequests[RequestKey]['response'] | Promise<BridgeRequests[RequestKey]['response']>
  >();
  private pendingRequests = new Map<string, PendingRequest<RequestKey>>();
  private errorListeners = new Set<(error: Error, context?: { message?: BridgeMessage }) => void>();

  static getInstance(): MessageBus {
    if (!this.instance) this.instance = new MessageBus();

    return this.instance;
  }

  onError(handler: (error: Error, context?: { message?: BridgeMessage }) => void): () => void {
    this.errorListeners.add(handler);

    return () => this.errorListeners.delete(handler);
  }

  onEvent<K extends EventKey>(
    event: K,
    handler: MessageHandler<BridgeEvents[K]>,
    onError?: (err: Error, msg?: BridgeMessage) => void
  ): () => void {
    let listeners = this.eventListeners.get(event);
    if (!listeners) {
      listeners = new Set();
      this.eventListeners.set(event, listeners);
    }

    const wrappedHandler = ((msg: BridgeMessage) => {
      try {
        handler(msg as EventMessage<BridgeEvents[K]>);
      } catch (err) {
        this.emitError(err, { message: msg as EventMessage<BridgeEvents[K]>, localErrorHandler: onError });
      }
    }) as (message: BridgeMessage) => void;

    listeners.add(wrappedHandler);

    return () => {
      listeners?.delete(wrappedHandler);
      if (listeners?.size === 0) this.eventListeners.delete(event);
    };
  }

  emit<K extends EventKey>(event: K, payload: BridgeEvents[K], source: string, target?: string): void {
    const message: EventMessage<BridgeEvents[K]> = {
      id: guid(),
      timestamp: Date.now(),
      source,
      target,
      type: 'event',
      event,
      payload,
    };

    this.deliverMessage(message);
  }

  onRequest<K extends RequestKey>(
    type: K,
    handler: RequestHandler<BridgeRequests[K]['request'], BridgeRequests[K]['response']>
  ): () => void {
    this.requestHandlers.set(type, handler);

    return () => {
      this.requestHandlers.delete(type);
    };
  }

  async request<K extends RequestKey>(
    type: K,
    payload: BridgeRequests[K]['request'],
    source: string,
    target?: string
  ): Promise<BridgeRequests[K]['response']> {
    const id = guid();

    const message: RequestMessage<BridgeRequests[K]['request']> = {
      id,
      timestamp: Date.now(),
      source,
      target,
      type: 'request',
      action: type,
      payload,
      expectsResponse: true,
      requestId: id,
    };

    return new Promise((resolve, reject) => {
      const timeout = setTimeout(() => {
        this.pendingRequests.delete(id);
        reject(new Error(`Request "${String(type)}" timed out`));
      }, RESPONSE_TIMEOUT_MS);

      this.pendingRequests.set(id, { resolve, reject, timeout } as PendingRequest<RequestKey>);
      this.deliverMessage(message);
    });
  }

  private respond<K extends RequestKey>(
    requestId: string,
    action: K,
    source: string,
    target: string | undefined,
    payload: BridgeRequests[K]['response']
  ): void {
    const message: ResponseMessage<BridgeRequests[K]['response']> = {
      id: guid(),
      timestamp: Date.now(),
      source,
      target,
      type: 'response',
      action,
      requestId,
      payload,
      success: true,
    };
    this.deliverMessage(message);
  }

  private deliverMessage(message: BridgeMessage): void {
    switch (message.type) {
      case 'event':
        this.handleEvent(message as EventMessage);
        break;

      case 'request':
        this.handleRequest(message as RequestMessage);
        break;

      case 'response':
        this.handleResponse(message as ResponseMessage);
        break;

      default:
        console.warn('[MessageBus] Unknown message type:', message);
    }
  }

  private handleEvent(message: EventMessage): void {
    const listeners = this.eventListeners.get(message.event);
    if (!listeners) return;

    for (const handler of listeners) {
      try {
        handler(message);
      } catch (err) {
        this.emitError(err, { message });
      }
    }
  }

  private handleRequest(message: RequestMessage): void {
    const action = message.action;
    const handler = this.requestHandlers.get(action);

    if (!handler) {
      this.respondError(
        message.id,
        action,
        message.source,
        message.target,
        `No handler registered for action "${String(action)}"`
      );

      return;
    }

    try {
      const result = handler(message.payload as BridgeRequests[typeof action]['request']);
      Promise.resolve(result)
        .then(response => {
          this.respond(message.id, action, message.source, message.target, response);
        })
        .catch((error: Error) => {
          this.respondError(message.id, action, message.source, message.target, error.message);
        });
    } catch (e) {
      const error = e instanceof Error ? e : new Error(String(e));
      this.respondError(message.id, action, message.source, message.target, error.message);
    }
  }

  private handleResponse(message: ResponseMessage): void {
    const pending = this.pendingRequests.get(message.requestId);
    if (!pending) return;

    clearTimeout(pending.timeout);
    this.pendingRequests.delete(message.requestId);
    if (message.success) {
      pending.resolve(message.payload as BridgeRequests[RequestKey]['response']);
    } else {
      pending.reject(new Error(message.error ?? 'Unknown error'));
    }
  }

  private respondError<K extends RequestKey>(
    requestId: string,
    action: K,
    source: string,
    target: string | undefined,
    errorMessage: string
  ): void {
    const message: ResponseMessage = {
      id: guid(),
      timestamp: Date.now(),
      source,
      target,
      type: 'response',
      action,
      requestId,
      payload: {},
      success: false,
      error: errorMessage,
    };
    this.deliverMessage(message);
  }

  private emitError(
    error: unknown,
    context?: { message?: BridgeMessage; localErrorHandler?: (err: Error, msg?: BridgeMessage) => void }
  ) {
    const err = error instanceof Error ? error : new Error(String(error));

    if (context?.localErrorHandler) {
      try {
        context.localErrorHandler(err, context.message);

        return;
      } catch (e) {
        console.error('[MessageBus] Error in localErrorHandler', e);
      }
    }

    if (this.errorListeners.size > 0) {
      for (const listener of this.errorListeners) {
        try {
          listener(err, context);
        } catch (e) {
          console.error('[MessageBus] Error in global error listener', e);
        }
      }

      return;
    }

    console.error('[MessageBus] Unhandled error', err, context?.message);
  }
}

export const messageBus = MessageBus.getInstance();
