import { autorun, makeAutoObservable, runInAction } from 'mobx';

import { REQUEST_PROFILE_CONTACT_STORE_EVENT, UPDATE_PROFILE_CONTACT } from '@/constants';
import type { ContactDataPayload } from '@/types';

import { PortalMessage } from '../messaging';
import { getContacts } from './fetchProfileContact';

export const requestProfileContactStoreAsync = async (): Promise<ProfileContactData> => {
  return new Promise(resolve => {
    PortalMessage.emitMessageAsync(REQUEST_PROFILE_CONTACT_STORE_EVENT, (profileContactStore: ProfileContactData) =>
      resolve(profileContactStore)
    );
  });
};

export class ProfileContactData {
  private _contactData: ContactDataPayload | null = null;
  private _tenantId: string;
  private _userId: string;
  private _disposer: (() => void) | null = null;
  private _isInitialLoad = true;

  constructor(tenantId: string, userId: string) {
    this._tenantId = tenantId;
    this._userId = userId;
    makeAutoObservable(this);
    this.setReactions();
  }

  public get contactData() {
    return this._contactData;
  }

  public updateContactData(contactData: ContactDataPayload) {
    runInAction(() => {
      this._contactData = contactData;
    });

    if (!this._isInitialLoad) {
      PortalMessage.emitMessageAsync(UPDATE_PROFILE_CONTACT, contactData);
    }
  }

  public updateContactDataSilent(contactData: ContactDataPayload) {
    runInAction(() => {
      this._contactData = contactData;
    });
  }

  public updateUser(tenantId: string, userId: string) {
    if (this._tenantId !== tenantId || this._userId !== userId) {
      runInAction(() => {
        this._tenantId = tenantId;
        this._userId = userId;
        this._contactData = null;
      });

      this.restartReactions();
    }
  }

  private setReactions() {
    this._disposer = autorun(async () => {
      if (this._tenantId && this._userId) {
        try {
          const fetched = await getContacts(this._tenantId, this._userId);
          runInAction(() => {
            this._contactData = fetched[0];
            this._isInitialLoad = false;
          });
        } catch (error) {
          console.error('Failed to fetch contact data', error);
        }
      }
    });
  }

  private restartReactions() {
    if (this._disposer) {
      this._disposer();
    }
    this.setReactions();
  }

  public subscribe() {
    PortalMessage.subscribe(REQUEST_PROFILE_CONTACT_STORE_EVENT, (e: CustomEvent) => {
      e.detail(this);
    });
  }

  public dispose() {
    if (this._disposer) {
      this._disposer();
      this._disposer = null;
    }
  }
}
