import { MessageBus } from './MessageBus';

export interface Bridge {
  messageBus: MessageBus;
}

declare global {
  interface Window {
    createPicheBridge?: () => Bridge;
    __picheBridge?: Bridge;
  }
}

const BRIDGE_KEY = Symbol.for('__picheBridge_internal');

export function initializeBridge(): void {
  if ((window as unknown as Record<symbol, unknown>)[BRIDGE_KEY]) {
    return;
  }

  const messageBus = MessageBus.getInstance();
  (window as unknown as Record<symbol, Bridge>)[BRIDGE_KEY] = { messageBus };
}

export function getBridge(): Bridge {
  const bridge = (window as unknown as Record<symbol, Bridge>)[BRIDGE_KEY];
  if (!bridge) {
    throw new Error('Bridge not initialized. Call initializeBridge() first.');
  }

  return bridge;
}
