import { requestApiClientAsync } from '@/services';
import type { IUserInfoRest } from '@/types';

const USERINFO_PATH = '/api/users/userinfo';

export const fetchUserInfo = async (apiUrl: string): Promise<IUserInfoRest> => {
  const userInfoUrl = `${apiUrl}${USERINFO_PATH}`;

  const client = await requestApiClientAsync();
  const response = await client.get<IUserInfoRest>(userInfoUrl);

  return response.data;
};
