import type { IListener, ISubscription, Listener } from '@/types';

export class MicroEmitter {
  private listeners: { [index: string]: IListener[] } = {};

  public addListener = (type: string, listener: Listener): MicroEmitter => {
    return this.storeListener(type, listener, false);
  };

  public on = (type: string, listener: Listener): MicroEmitter => {
    return this.addListener(type, listener);
  };

  public subscribe = (type: string, listener: Listener): ISubscription => {
    this.on(type, listener);

    return { unsubscribe: () => this.off(type, listener) };
  };

  public addOnceListener = (type: string, listener: Listener): MicroEmitter => {
    return this.storeListener(type, listener, true);
  };

  public once = (type: string, listener: Listener): MicroEmitter => {
    return this.addOnceListener(type, listener);
  };

  public removeListener = (type: string, listener?: Listener): MicroEmitter => {
    if (!this.listeners[type]) {
      return this;
    }

    if (!this.listeners[type].length) {
      return this;
    }

    if (!listener) {
      delete this.listeners[type];

      return this;
    }

    this.listeners[type] = this.listeners[type].filter(l => !(l.listener === listener));

    return this;
  };

  public off(type: string, listener: Listener): MicroEmitter {
    return this.removeListener(type, listener);
  }

  public emit = (type: string, payload?: any): MicroEmitter => {
    if (!this.listeners[type]) {
      return this;
    }
    this.listeners[type].forEach(l => {
      Promise.resolve().then(() => {
        l.listener.apply(this, [payload]);
        if (l.once) {
          this.removeListener(type, l.listener);
        }
      });
    });

    return this;
  };

  private storeListener = (type: string, listener: Listener, once: boolean): MicroEmitter => {
    this.listeners[type] = this.listeners[type] || [];
    this.listeners[type].push({ listener, once });

    return this;
  };
}
