import type { AxiosResponse } from 'axios';

import { MAX_ITEMS, REQUEST_TABLE_CLIENT_EVENT, STATUS_OK } from '@/constants';
import { doAxiosAsync, doAxiosWithResult } from '@/services';
import type {
  BulkInsertResult,
  FilterParameters,
  GenericObjectType,
  OperationResult,
  PaginationType,
  Subscriber,
  Table,
  TableSingle,
} from '@/types';

import { PortalMessage } from '../messaging';
import { requestApiConfigAsync } from '../subscribers';
import { QueryBuilder } from './QueryBuilder';

export class TableClient implements Subscriber {
  public subscribe() {
    PortalMessage.subscribe(REQUEST_TABLE_CLIENT_EVENT, (e: CustomEvent) => e.detail(this));
  }

  public async getAsync<T>(table: string, id: string): Promise<TableSingle<T>> {
    const url = await this.getUrl(table, id);
    const result = await doAxiosAsync<TableSingle<T>>(async axios => await axios.get<TableSingle<T>>(url));

    if (result.status == STATUS_OK) {
      return result.data;
    }

    throw new Error('Record not found');
  }

  public async getAllAsync<T>(table: string, top: PaginationType = MAX_ITEMS, skip = 0): Promise<Table<T>> {
    const params: Partial<FilterParameters> = {
      skip,
      top,
    };

    const query = QueryBuilder.buildQuery(params);
    let url = await this.getUrl(table);

    url = `${url}${query}`;

    const result = await doAxiosAsync<Table<T>>(async axios => await axios.get<Table<T>>(url));

    return this.getDataOrThrowRecordNotFound(result);
  }

  public async filterAsync<T>(params: Partial<FilterParameters>): Promise<Table<T>> {
    const { table } = params;

    if (!table) {
      throw new Error('Table name is required');
    }

    const query = QueryBuilder.buildQuery(params);
    let url = await this.getUrl(table);

    url = `${url}${query}`;

    const result = await doAxiosAsync<Table<T>>(async axios => await axios.get<Table<T>>(url));

    return this.getDataOrThrowRecordNotFound(result);
  }

  public async updateAsync(table: string, id: string, body: GenericObjectType): Promise<OperationResult> {
    const url = await this.getUrl(table, id);

    return await doAxiosWithResult(async axios => await axios.put(url, body));
  }

  public async insertAsync(table: string, body: GenericObjectType): Promise<OperationResult> {
    const url = await this.getUrl(table);

    return await doAxiosWithResult(async axios => await axios.post(url, body));
  }

  public async bulkInsertAsync(table: string, body: GenericObjectType[]): Promise<BulkInsertResult[]> {
    const url = await this.getUrl(table);

    const requestResults = await this.insertRowsAsync(body, url);

    return this.formatInsertResults(requestResults);
  }

  public async deleteOneAsync(table: string, id: string): Promise<OperationResult> {
    return await this.deleteManyAsync(table, [id]);
  }

  public async deleteManyAsync(table: string, records: Array<string>): Promise<OperationResult> {
    const url = await this.getUrl(table);

    return await doAxiosWithResult(async axios => await axios.delete(url, { data: records }));
  }

  public async getUrl(table: string, id = ''): Promise<string> {
    const config = await requestApiConfigAsync();

    return `${config.apiUrl}/api/tables/${table}/rows${id === '' ? '' : `/${id}`}`;
  }

  private getDataOrThrowRecordNotFound<T>(result: AxiosResponse<Table<T>>): Table<T> {
    if (result.status == STATUS_OK) {
      return result.data;
    }
    throw new Error('Record not found');
  }

  private async insertRowsAsync(body: GenericObjectType[], url: string): Promise<OperationResult[]> {
    const promises = [];
    for (let i = 0; i < body.length; i++) {
      promises.push(doAxiosWithResult(async axios => await axios.post(url, body[i])));
    }

    const requestResults = await Promise.all(promises);

    return requestResults;
  }

  private formatInsertResults(results: OperationResult[]): BulkInsertResult[] {
    return results.map(result => {
      return {
        rowId: result.response?.resourceId,
        success: result.statusCode,
        error: result.error,
      };
    });
  }
}

export const tableClient = new TableClient();
