import { REQUEST_TABLE_DATA_LOADER } from '@/constants';
import { requestApiClientAsync } from '@/services';
import type { Subscriber } from '@/types';

import { PortalMessage } from '../messaging';
import { TableCache, tableCache } from './TableCache';

type RequestScope = {
  ids: {
    [key: string]: {
      promise: Promise<any>;
      resolver: {
        resolve: (value: any) => void;
        reject: (error: Error) => void;
      };
    };
  };
  timeout: any | null;
};

/*
apiTemplateUrl - full URL template which must include "[table] which will be replaced on request
example http://localhost:5008/api/Tables/[table]/batch-query
 */
type TableDataLoaderConfig = {
  maxBatchSize: number;
  debounceTime: number;
  cacheClearTime: number;
  apiTemplateUrl: string;
};

export class TableDataLoader implements Subscriber {
  private requestQueue: { [key: string]: RequestScope } = {};
  private configuration: TableDataLoaderConfig;
  private readonly tableCache?: TableCache = new TableCache();

  constructor(configuration: TableDataLoaderConfig) {
    this.configuration = configuration;
    this.tableCache = tableCache;
  }

  public subscribe() {
    PortalMessage.subscribe(REQUEST_TABLE_DATA_LOADER, event => {
      event.detail(this);
    });
  }

  public async request<T>(table: string, id: string): Promise<T> {
    if (this.tableCache) {
      const cachedData = this.tableCache.get<T>(table, id);
      if (cachedData) {
        return Promise.resolve(cachedData);
      }
    }

    if (!this.requestQueue[table]) {
      this.requestQueue[table] = {
        ids: {},
        timeout: null,
      };
    }

    const queue = this.requestQueue[table];

    if (queue.ids[id] !== undefined) {
      return queue.ids[id].promise;
    }

    queue.ids[id] = {} as any;

    const promise = new Promise<T>((resolve, reject) => {
      queue.ids[id].resolver = { resolve, reject };
    });

    queue.ids[id].promise = promise;

    if (Object.keys(queue.ids).length >= this.configuration.maxBatchSize) {
      await this.executeBatchRequest(table);
    } else {
      if (queue.timeout) {
        clearTimeout(queue.timeout);
      }

      queue.timeout = setTimeout(async () => {
        await this.executeBatchRequest<T>(table);
      }, this.configuration.debounceTime);
    }

    return promise;
  }

  private async executeBatchRequest<T>(table: string): Promise<void> {
    const queue = this.requestQueue[table];
    if (queue.timeout) {
      clearTimeout(queue.timeout);
      queue.timeout = null;
    }
    const ids = queue.ids;
    queue.ids = {};

    const axios = await requestApiClientAsync();
    const axiosResponse = await axios.post<{ rows: Array<T> }>(
      this.configuration.apiTemplateUrl.replace('[table]', table),
      Object.keys(ids).map(k => k)
    );

    const response = axiosResponse.data;

    if (response != undefined) {
      const resultMap = new Map<string, T>();
      response.rows.forEach((row: any) => {
        resultMap.set(row.id, row);
      });

      Object.keys(ids).forEach(id => {
        const result = resultMap.get(id);
        ids[id].resolver.resolve(result);

        if (this.tableCache) {
          this.tableCache.set(table, id, result, this.configuration.cacheClearTime);
        }
      });
    } else {
      Object.keys(ids).forEach(id => {
        ids[id].resolver.resolve(undefined);
      });
    }
  }
}
