import {
  LOGIN_SUCCESSFULLY_FOR_PORTAL_USER,
  LOGOUT_SUCCESSFULLY_FOR_PORTAL_USER,
  REQUEST_PORTAL_USER_MANAGER,
} from '@/constants';
import { logoutFromKeycloak } from '@/features/user/adapters';
import { doAxiosWithResult } from '@/services';
import type {
  Consumer,
  GenericObjectType,
  IIdentityService,
  ILoginPayload,
  ILoginPayloadWithCode,
  IRestTokenResponse,
  IUserInfo,
  OperationResult,
  Subscriber,
} from '@/types';
import { AUTHENTICATION_TYPE } from '@/types';

import { LogError, LogInfo } from '../logger';
import { PortalMessage } from '../messaging';
import WebSessionStorage from '../storage/WebSessionStorage';
import { requestApiConfigAsync } from '../subscribers';
import { fetchUserInfo, loginWithCode, loginWithPassword } from './adapters';
import { TENANT_ID } from './config';

export const requestUserManagerAsync = (): Promise<UserManager> => {
  return new Promise(resolve => {
    const userManager: Consumer<UserManager> = (manager: UserManager) => {
      resolve(manager);
    };

    PortalMessage.emitMessageAsync(REQUEST_PORTAL_USER_MANAGER, { consumerUserManager: userManager });
  });
};

export class UserManager implements Subscriber {
  private accessToken?: string;
  private refreshToken?: string;
  private expiresIn?: Date;
  private userInformation?: IUserInfo;
  public readonly identityService: IIdentityService;
  public tenantId = TENANT_ID;

  public userInfo = (): IUserInfo | undefined => {
    return this.userInformation;
  };

  public get IsAuthenticated(): boolean {
    if (!this.accessToken || !this.expiresIn) {
      return false;
    }

    return new Date() < this.expiresIn;
  }

  public get AuthorizationToken(): string | undefined {
    return this.accessToken;
  }

  public get RefreshToken(): string | undefined {
    return this.refreshToken;
  }

  constructor(identityService: IIdentityService) {
    this.identityService = identityService;
  }

  public subscribe() {
    this.setupEventListener();
  }

  public loginWithCodeAsync = async (payload: ILoginPayloadWithCode): Promise<boolean> => {
    return this.processLogin(payload, payload.type, loginWithCode);
  };

  public loginAsync = async (payload: ILoginPayload): Promise<boolean> => {
    return this.processLogin(payload, AUTHENTICATION_TYPE.password, loginWithPassword);
  };

  private async processLogin<T extends ILoginPayload | ILoginPayloadWithCode>(
    payload: T,
    expectedAuthType: AUTHENTICATION_TYPE,
    loginMethod: (payload: T, identityService: IIdentityService) => Promise<IRestTokenResponse>
  ): Promise<boolean> {
    if (payload.type !== expectedAuthType) {
      return false;
    }

    try {
      const loginResult = await loginMethod(payload, this.identityService);
      this.storeToken(loginResult, payload.clientId);

      try {
        await this.getUserInformation();
      } catch (e) {
        LogError('Unable to retrieve user information', 'user_info_fetch_error', e as Error, 'UserManager');

        return false;
      }

      PortalMessage.emitMessageAsync(LOGIN_SUCCESSFULLY_FOR_PORTAL_USER, this.userInformation);
      LogInfo('Authentication successful', 'authentication_successful', undefined, 'UserManager');

      return true;
    } catch (e) {
      LogError('Authentication failed', 'authentication_unsuccessful', e as Error, 'UserManager');

      return false;
    }
  }

  public tryAutoLogin = async (): Promise<void> => {
    const accessToken = WebSessionStorage.get<string>(`${this.identityService.storageKey}.access_token`);
    const expiresIn = WebSessionStorage.get<Date>(`${this.identityService.storageKey}.expireDate`);
    const tenantId = WebSessionStorage.get<string>(`${this.identityService.storageKey}.tenantId`);

    if (!expiresIn) {
      return Promise.resolve();
    }

    const authenticated = new Date() < new Date(expiresIn);

    if (authenticated) {
      this.accessToken = accessToken || undefined;
      this.expiresIn = new Date(expiresIn);
      this.tenantId = tenantId ?? '';

      try {
        await this.getUserInformation();
      } catch (e) {
        LogError('Unable to retrieve user information', 'user_info_fetch_error', e as Error, 'UserManager');

        this.logout();
      }
    }

    return Promise.resolve();
  };

  public logout = async () => {
    const userInformation = this.userInformation;

    if (this.refreshToken) {
      try {
        await logoutFromKeycloak(this.identityService, this.tenantId, this.refreshToken);
      } catch (e) {
        LogError('KC logout failed', 'kc_logout_failed', e as Error, 'UserManager');
      }
    }

    this.removeSessionFromBrowser();
    this.accessToken = undefined;
    this.refreshToken = undefined;
    this.expiresIn = undefined;
    this.userInformation = undefined;

    PortalMessage.emitMessageAsync(LOGOUT_SUCCESSFULLY_FOR_PORTAL_USER, userInformation);
  };

  public async resetPasswordAsync(email: string, tenantId: string = TENANT_ID): Promise<OperationResult> {
    const config = await requestApiConfigAsync();
    const url = `${config.apiUrl}/api/users/${tenantId ?? this.tenantId}/${email}/reset`;

    return await doAxiosWithResult(async axios => await axios.get(url));
  }

  public async updatePasswordAsync(body: GenericObjectType, tenantId: string = TENANT_ID): Promise<OperationResult> {
    const config = await requestApiConfigAsync();
    const url = `${config.apiUrl}/api/users/${tenantId}/update-password`;

    return await doAxiosWithResult(async axios => await axios.post(url, body));
  }

  public async registerAsync(body: GenericObjectType, token: string): Promise<OperationResult> {
    const config = await requestApiConfigAsync();

    const url = `${config.apiUrl}/api/public/${this.tenantId}/user-invites/${token}`;

    return await doAxiosWithResult(async axios => await axios.post(url, body));
  }

  private removeSessionFromBrowser(): void {
    WebSessionStorage.remove(`${this.identityService.storageKey}.access_token`);
    WebSessionStorage.remove(`${this.identityService.storageKey}.refresh_token`);
    WebSessionStorage.remove(`${this.identityService.storageKey}.expireDate`);
    WebSessionStorage.remove(`${this.identityService.storageKey}.tenantId`);
  }

  public storeToken(loginResult: IRestTokenResponse, tenantId: string): void {
    const accessToken = loginResult.access_token;
    const refreshToken = loginResult.refresh_token;
    const expireDate = new Date(new Date().getTime() + loginResult.expires_in * 1000);

    WebSessionStorage.set(`${this.identityService.storageKey}.access_token`, accessToken);
    WebSessionStorage.set(`${this.identityService.storageKey}.refresh_token`, refreshToken);
    WebSessionStorage.set(`${this.identityService.storageKey}.expireDate`, expireDate.toString());
    WebSessionStorage.set(`${this.identityService.storageKey}.tenantId`, tenantId);

    this.accessToken = accessToken;
    this.refreshToken = refreshToken;
    this.expiresIn = expireDate;
    this.tenantId = tenantId;
  }

  private getUserInformation = async (): Promise<void> => {
    const config = await requestApiConfigAsync();
    const { name, role, sub } = await fetchUserInfo(config.apiUrl);
    const roles = typeof role === 'string' ? [role] : role;

    this.userInformation = {
      id: sub,
      name: name,
      roles: roles,
      tenantId: this.tenantId,
    };
  };

  private setupEventListener = (): void => {
    PortalMessage.subscribe(REQUEST_PORTAL_USER_MANAGER, e => {
      e.detail.consumerUserManager(this);
    });
  };
}
