import type { FilterParameters } from '@/types';

export class QueryBuilder {
  public static buildQuery = (params: Partial<FilterParameters>): string => {
    const { skip = 0, search = '', orderBy = '', top = 30, filter = '', select = '' } = params;

    const searchParams = ['skip=' + skip, 'top=' + top];

    if (search !== '') {
      searchParams.push('search=' + search);
    }

    if (orderBy !== '') {
      searchParams.push('orderBy=' + orderBy);
    }

    if (filter !== '') {
      searchParams.push('filter=' + filter);
    }

    if (select !== '') {
      searchParams.push('select=' + select);
    }

    const query = '?' + searchParams.filter(f => f).join('&');

    return query;
  };
}
