import type { UserManager } from '../features/user';

export enum LogLevel {
  EMERGENCY = 0,
  ALERT = 1,
  CRITICAL = 2,
  ERROR = 3,
  WARN = 4,
  INFO = 6,
  DEBUG = 7,
}

export interface ILogIdentity {
  userId: string;
}

export interface ILogMessage {
  id: string;
  version: string;
  loggerId: string;
  timestamp: number;
  identity: ILogIdentity;
  text: string;
  code: string;
  logLevel: LogLevel;
  error?: {
    message: string;
    stack?: string;
  };
}

export interface ILogRemoteOptions {
  url: string;
  headers?: { [index: string]: string };
  batchSize?: number;
  sendIntervalInSeconds?: number;
}

export interface ILoggerOptions {
  remote: ILogRemoteOptions;
  userManager: UserManager;
}

export interface ILogMessageDetail {
  message: string;
  code: string;
  level: LogLevel;
  error?: Error;
  nameSpace?: string;
}
