import { useEffect, useRef } from 'react';

import type { BridgeRequests, RequestHandler } from '@/types';

import { useMessageBus } from './useMessageBus';

export function useBusRequest<K extends keyof BridgeRequests>(
  request: K,
  handler: RequestHandler<BridgeRequests[K]['request'], BridgeRequests[K]['response']>
): void {
  const messageBus = useMessageBus();
  const handlerRef = useRef(handler);

  useEffect(() => {
    handlerRef.current = handler;
  }, [handler]);

  useEffect(() => {
    const unsubscribe = messageBus?.onRequest(request, msg => {
      return handlerRef.current(msg);
    });

    return unsubscribe;
  }, [request, messageBus]);
}
