/* tslint:disable:no-console */
import { Optional } from 'typescript-optional';

import { LOGGER_EVENT_LISTENER, STORAGE_LOGGER_NAME_SPACE_PREFIX, UNKNOWN } from '@/constants';
import { MicroEmitter } from '@/services';
import type { ILoggerOptions, ILogIdentity, ILogMessage, ILogMessageDetail } from '@/types';
import { LogLevel } from '@/types';
import { guid, unixTimestampUTC } from '@/utils';

import { PortalMessage } from '../messaging';
import postLogs from './adapters/postLogs';

const STORAGE_LOGGER_ID = 'logger.id';
const STORAGE_LOGGER_CONSOLE = 'logger.console';

/**
 * Log level as in RFC3164 https://tools.ietf.org/html/rfc3164
 */

const CONSOLES: { [index: number]: (message: ILogMessage) => void } = {
  [LogLevel.INFO]: console.info,
  [LogLevel.ERROR]: console.error,
  [LogLevel.DEBUG]: console.debug,
  [LogLevel.WARN]: console.warn,
  [LogLevel.ALERT]: console.info,
  [LogLevel.EMERGENCY]: console.error,
  [LogLevel.CRITICAL]: console.error,
};

export class Logger {
  private static WRITE_LOG_EVENT = 'write_log_event';

  private readonly loggerId: string;
  private options: ILoggerOptions;
  private messages: ILogMessage[] = [];
  private interval: any;
  private events: MicroEmitter = new MicroEmitter();
  private lastTimeLogsWasSent = 0;

  constructor(options: ILoggerOptions) {
    this.loggerId = this.getLoggerId();
    this.options = options;
    this.events.subscribe(Logger.WRITE_LOG_EVENT, this.handleConsole);
    this.events.subscribe(Logger.WRITE_LOG_EVENT, this.handleRemote);
  }

  public getKey = (key: string): Optional<string> => Optional.ofNullable(localStorage.getItem(key));
  public setKey = (key: string, value: string) => localStorage.setItem(key, value);
  public isConsoleOutputEnabled = (): boolean => this.getKey(STORAGE_LOGGER_CONSOLE).orElse('false') === 'true';

  public enableRemoteLogging = (): Logger => {
    if (!this.options.remote) {
      throw Error('Remote options not set');
    }
    this.startLogSending();

    return this;
  };

  public log = (text: string, code: string, logLevel: LogLevel, error?: Error, nameSpace?: string): ILogMessage => {
    const message: ILogMessage = {
      id: guid(),
      version: '1.0',
      loggerId: this.loggerId,
      identity: this.getLogIdentity(),
      text,
      code,
      logLevel,
      timestamp: unixTimestampUTC(),
    };

    if (error) {
      message.error = {
        message: error.message || '',
        stack: error.stack || '',
      };
    }

    this.write(message, logLevel, nameSpace || '');

    return message;
  };

  public getStoredLogLevel = (key: string): false | LogLevel => {
    const storedLevel = this.getKey(key);
    if (!storedLevel.isPresent()) {
      return false;
    }
    const level: any = LogLevel[storedLevel.get() as any];

    return level ? (level as LogLevel) : LogLevel.ERROR;
  };

  public enableEventDrivenLogging = (): Logger => {
    const event = (e: CustomEvent) => {
      const detail = e.detail as ILogMessageDetail;
      setTimeout(() => this.log(detail.message, detail.code, detail.level, detail.error, detail.nameSpace), 0);
    };

    PortalMessage.subscribe(LOGGER_EVENT_LISTENER, event);

    return this;
  };

  public canWriteLog = (logMessageLogLevel: LogLevel, nameSpace: string): boolean => {
    const globalLogLevel = this.getStoredLogLevel(`${STORAGE_LOGGER_NAME_SPACE_PREFIX}*`);
    const namespaceLogLevel = this.getStoredLogLevel(`${STORAGE_LOGGER_NAME_SPACE_PREFIX}${nameSpace || '*'}`);

    if (namespaceLogLevel) {
      return logMessageLogLevel <= namespaceLogLevel;
    }

    if (globalLogLevel) {
      return logMessageLogLevel <= globalLogLevel;
    }

    return logMessageLogLevel <= LogLevel.INFO;
  };

  private write = (message: ILogMessage, logLevel: LogLevel, nameSpace: string) => {
    if (this.canWriteLog(logLevel, nameSpace)) {
      this.events.emit(Logger.WRITE_LOG_EVENT, message);
    }
  };

  private handleConsole = (message: ILogMessage) => {
    if (this.isConsoleOutputEnabled()) {
      CONSOLES[message.logLevel](message);
    }
  };

  private handleRemote = (message: ILogMessage) => {
    if (this.interval) {
      this.resetLastTimeLogsWasSent();
      this.messages.push(message);
    }
  };

  private resetLastTimeLogsWasSent = () => {
    if (this.messages.length === 0) {
      this.lastTimeLogsWasSent = unixTimestampUTC();
    }
  };

  private getLogIdentity = (): ILogIdentity => {
    const logIdentity: ILogIdentity = { userId: UNKNOWN };
    if (
      this.options.userManager &&
      this.options.userManager.IsAuthenticated &&
      this.options.userManager.userInfo() !== undefined
    ) {
      logIdentity.userId = this.options.userManager.userInfo()?.id || UNKNOWN;
    }

    return logIdentity;
  };

  private getLoggerId = (): string => {
    const optionalId = this.getKey(STORAGE_LOGGER_ID);

    return optionalId.orElseGet(() => this.createLoggerId());
  };

  private createLoggerId = (): string => {
    const id = guid();
    this.setKey(STORAGE_LOGGER_ID, id);

    return id;
  };

  private startLogSending = () => {
    clearInterval(this.interval);
    this.interval = setInterval(this.checkForLogsToSend, 1000);
  };

  private checkForLogsToSend = () => {
    if (!this.isThereSomethingToSend()) {
      return;
    }

    const messages = this.cloneLogMessages();
    this.clearLogMessages();

    const sendLogs = async (throttle: number) => {
      try {
        await postLogs(messages, this.options.remote);
      } catch (error) {
        this.log('Log send error', 'log_send_error', LogLevel.ERROR, error as Error);
        setTimeout(() => sendLogs(Math.pow(2, throttle)), throttle * 1000);
      }
    };

    setTimeout(() => sendLogs(1), 0);
  };

  private cloneLogMessages = () => this.messages.slice(0);

  private clearLogMessages = () => {
    this.messages = [];
  };

  private isSendTimeReached = () => {
    const sendInterval = this.options.remote.sendIntervalInSeconds || 60;
    const currentTime = unixTimestampUTC();

    return currentTime - this.lastTimeLogsWasSent > sendInterval;
  };

  private isThereSomethingToSend = () => {
    const length = this.messages.length;
    const batchSize = this.options.remote.batchSize || 0;

    return length !== 0 && (length >= batchSize || this.isSendTimeReached());
  };
}
