import './AppLoadingScreen.scss';

import { clsx } from 'clsx';
import type { FC } from 'react';
import { useEffect, useState } from 'react';

import { PortalMessage } from '@/features';
import type { WithChildren } from '@/types';

const PICHE_EVENT_APP_LOADING = 'PICHE_EVENT_APP_LOADING';
const showAppLoading = () => PortalMessage.emitMessageAsync(PICHE_EVENT_APP_LOADING, true);
const hideAppLoading = () => PortalMessage.emitMessageAsync(PICHE_EVENT_APP_LOADING, false);

export type Props = {
  loading: boolean;
};

const AppLoadingScreen: FC<WithChildren & Props> = ({ children, loading }) => {
  const [visible, setVisible] = useState(loading);

  useEffect(() => {
    const subscription = PortalMessage.subscribe(PICHE_EVENT_APP_LOADING, (e: CustomEvent) => setVisible(e.detail));

    return () => {
      subscription.unsubscribe();
    };
  }, []);

  return (
    <>
      <div className={clsx({ 'h-100 w-100 d-flex': !visible }, { 'd-none': visible })}>{children}</div>

      {visible && (
        <div className='pl__wrapper w-100 h-100'>
          <div className='pl'>
            <div className='pl__outer-ring'></div>
            <div className='pl__inner-ring'></div>
            <div className='pl__track-cover'></div>
            <div className='pl__ball'>
              <div className='pl__ball-texture'></div>
              <div className='pl__ball-outer-shadow'></div>
              <div className='pl__ball-inner-shadow'></div>
              <div className='pl__ball-side-shadows'></div>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export { AppLoadingScreen, hideAppLoading, showAppLoading };
