export interface IUserInfoRest {
  name: string;
  sub: string;
  role: string | string[];
}

export enum AUTHENTICATION_TYPE {
  password = 'password',
  AD = 'AD',
  microsoftEntraId = 'microsoftEntraId',
  google = 'google',
  refreshToken = 'refresh_token',
}

export interface ILoginPayload {
  userName: string;
  password: string;
  scope: string;
  clientId: string;
  code?: string;
  type: AUTHENTICATION_TYPE;
}

export interface ILoginPayloadWithCode {
  scope: string;
  clientId: string;
  type: AUTHENTICATION_TYPE;
  code: string;
  redirectUri: string;
}

export interface IIdentityService {
  location: string;
  storageKey: string;
  logoutRedirectRoute: string;
}

export interface IRestTokenPayload {
  client_id: string;
  refresh_token: string;
}

export interface IRestTokenResponse {
  access_token: string;
  expires_in: number;
  token_type: string;
  refresh_token: string;
  refresh_expires_in: number;
}

export interface IUserInfo {
  id: string;
  name: string;
  roles: string[];
  tenantId: string;
}
