import type { AxiosResponse } from 'axios';
import axios from 'axios';
import type { Emitter } from 'nanoevents';
import { createNanoEvents } from 'nanoevents';

import { REQUEST_API_CLIENT_EVENT } from '@/constants';
import { PortalMessage } from '@/features/messaging';
import WebSessionStorage from '@/features/storage/WebSessionStorage';
import type { UserManager } from '@/features/user';
import { updateAccessToken } from '@/features/user/adapters';
import type {
  AxiosInstanceWithOperations,
  Func,
  IIdentityService,
  IOperationResponse,
  IRestTokenPayload,
  OperationResult,
  Subscriber,
} from '@/types';

import { waitForOperation } from './OperationClient';
import { RequestQueue } from './RequestQueue';

export type ApiClientEmitter = Emitter<{ 'no-pending-create-operation-requests': () => void }>;

export const requestApiClientAsync = async (): Promise<AxiosInstanceWithOperations> => {
  return new Promise(resolve => {
    PortalMessage.emitMessageAsync(REQUEST_API_CLIENT_EVENT, (axiosInstance: AxiosInstanceWithOperations) =>
      resolve(axiosInstance)
    );
  });
};

export class ApiClient implements Subscriber {
  public getLocale = () => this.locale;
  public setLocale = (locale: string) => (this.locale = locale);
  public getAxiosInstance = (): AxiosInstanceWithOperations | undefined => this.instance;

  public readonly identityService: IIdentityService;
  private instance?: AxiosInstanceWithOperations;
  private locale = 'lv-LV';
  private userManager?: UserManager;

  constructor(identityService: IIdentityService) {
    this.identityService = identityService;

    setInterval(() => this.cleanupOperations(), 10 * 60 * 1000);
  }

  public setup(userManager: UserManager) {
    this.userManager = userManager;
    this.instance = Object.assign(axios.create({ baseURL: import.meta.env.VITE_API_URL }), {
      operationIds: [],
      pendingCreateOperationRequests: 0,
      emitter: createNanoEvents() as ApiClientEmitter,
    }) as AxiosInstanceWithOperations;

    this.instance.interceptors.request.use(request => {
      if (!request.url?.includes('/protocol/openid-connect/')) {
        request.headers['x-piche-locale'] = this.locale;
      }

      if (this.userManager?.IsAuthenticated) {
        request.headers['Authorization'] = `Bearer ${this.userManager.AuthorizationToken}`;
      }

      return request;
    });

    const requestQueue = new RequestQueue();

    this.instance.interceptors.response.use(
      response => response,
      async error => {
        try {
          if (error.response?.status === 401) {
            return await this.onRejected(error, requestQueue);
          }
          throw error;
        } catch (e) {
          return Promise.reject(e);
        }
      }
    );
  }

  private cleanupOperations() {
    if (!this.instance) {
      return;
    }

    const now = Date.now();
    this.instance.operationIds = this.instance.operationIds.filter(
      operationId => now - operationId.time <= 10 * 60 * 1000
    );
  }

  private onRejected = async (error: any, queue: RequestQueue) => {
    const originalRequest = error.config;

    const storageKey = this.identityService.storageKey;
    const refreshToken = WebSessionStorage.get<string>(`${storageKey}.refresh_token`);

    if (!refreshToken) {
      throw new Error('Session expired');
    }

    if (!queue.isTripWire) {
      queue.isTripWire = true;

      const refreshPayload: IRestTokenPayload = {
        client_id: this.userManager?.tenantId || '',
        refresh_token: refreshToken,
      };

      const refreshResponse = await updateAccessToken(refreshPayload, this.identityService);

      this.userManager?.storeToken(refreshResponse, refreshPayload.client_id);

      queue.isTripWire = false;

      while (queue.length > 0) {
        const waitingRequest = queue.dequeue();

        if (waitingRequest) {
          waitingRequest.config.headers.Authorization = `${refreshResponse.token_type} ${refreshResponse.access_token}`;

          await this.instance?.request(waitingRequest.config);
        }
      }

      return this.instance?.request(originalRequest);
    } else {
      return new Promise((resolve, reject) => {
        queue.enqueue({ resolve, reject, config: originalRequest });
      });
    }
  };

  public subscribe() {
    PortalMessage.subscribe(REQUEST_API_CLIENT_EVENT, (e: CustomEvent) => {
      e.detail(this.instance);
    });
  }
}

/***
 * any operation with pich.api
 *
 * usage
 * await doAxiosAsync<{ id: string }>(async s => {
 *   const response = await s.get<{ id: string }>('link');
 *   return response;
 * });
 * @param consumer
 */
const doAxiosAsync = async <T>(
  consumer: Func<AxiosInstanceWithOperations, Promise<AxiosResponse<T>>>
): Promise<AxiosResponse<T>> => {
  const axios = await requestApiClientAsync();

  return await consumer(axios);
};

/***
 * async interaction with piche.api for delete, put, post
 * waits for response returns the status
 *
 * usage
 * await doAxiosWithResult(async axios => {
 *   return await axios.put('link', { id: 1 });
 * });
 * @param consumer
 */
const doAxiosWithResult = async (
  consumer: Func<AxiosInstanceWithOperations, Promise<AxiosResponse<IOperationResponse>>>
): Promise<OperationResult> => {
  const axios = await requestApiClientAsync();

  axios.pendingCreateOperationRequests += 1;

  return await waitForOperation(axios, async () => await consumer(axios));
};

export { doAxiosAsync, doAxiosWithResult };
