import './Login.scss';

import type { ChangeEvent, FC, FormEvent } from 'react';
import { useState } from 'react';

import { AUTHENTICATION_TYPE } from '@/types';

import { requestUserManagerAsync } from '../user';
import { useAuth } from './AuthContext.ts';

export const Login: FC = () => {
  const [email, setEmail] = useState<string>('');
  const [password, setPassword] = useState<string>('');
  const [isUserRemembered, setIsUserRemembered] = useState<boolean>(true);
  const [loading, setLoading] = useState<boolean>(false);
  const { setCurrentUser } = useAuth();

  const login = async (e: FormEvent<HTMLFormElement>): Promise<void> => {
    e.preventDefault();
    try {
      setLoading(true);

      const userManager = await requestUserManagerAsync();

      await userManager.loginAsync({
        scope: `email openid profile`,
        clientId: 'piche',
        password: password,
        userName: email,
        type: AUTHENTICATION_TYPE.password,
      });

      const user = userManager.userInfo();

      setCurrentUser(user);
    } finally {
      setLoading(false);
    }
  };

  const handleEmailChange = (e: ChangeEvent<HTMLInputElement>): void => {
    setEmail(e.target.value);
  };
  const handlePasswordChange = (e: ChangeEvent<HTMLInputElement>): void => {
    setPassword(e.target.value);
  };

  const handleCheckboxChange = (e: ChangeEvent<HTMLInputElement>): void => {
    setIsUserRemembered(e.target.checked);
  };

  return (
    <div className='piche-login'>
      <form
        className='form'
        onSubmit={login}
      >
        <div className='form__text-wrapper'>
          <h1 className='form__title'>Sign In</h1>
          <div className='form__subtitle'>Piche Bosio</div>
        </div>
        <div className='form__group'>
          <label htmlFor='email'>Email address</label>
          <input
            name='email'
            type='email'
            id='email'
            value={email}
            onChange={handleEmailChange}
            placeholder='Enter email'
            autoComplete='email'
          />
        </div>
        <div className='form__group'>
          <label htmlFor='password'>Password</label>
          <input
            name='password'
            type='password'
            id='password'
            value={password}
            onChange={handlePasswordChange}
            placeholder='Password'
            autoComplete='current-password'
          />
        </div>
        <div className='form__remember-me'>
          <input
            type='checkbox'
            checked={isUserRemembered}
            onChange={handleCheckboxChange}
            id='remember-me'
          />
          <label htmlFor='remember-me'>Remember Me</label>
        </div>
        <button
          disabled={loading}
          type='submit'
        >
          Continue
        </button>
      </form>
    </div>
  );
};
