import { useEffect, useState } from 'react';

import type { IUserInfo } from '@/types';

import { LogError } from '../logger';
import { requestUserManagerAsync } from '../user';
import { AuthContext } from './AuthContext';
import { Login } from './Login';

interface AuthProviderProps {
  children: React.ReactNode;
  withLogin?: boolean;
}

const AuthProvider: React.FC<AuthProviderProps> = ({ children, withLogin }) => {
  const [currentUser, setCurrentUser] = useState<IUserInfo | undefined>();
  const [currentUserLoading, setCurrentUserLoading] = useState(true);

  const logout = () => {
    setCurrentUser(undefined);
    requestUserManagerAsync().then(manager => manager.logout());
  };

  useEffect(() => {
    setCurrentUserLoading(true);
    try {
      requestUserManagerAsync()
        .then(manager => manager.userInfo && setCurrentUser(manager.userInfo()))
        .finally(() => setCurrentUserLoading(false));
    } catch (e) {
      setCurrentUserLoading(false);
      LogError('LoginPage init error', 'login_init', e as Error, 'AuthInit');
    }
  }, []);

  const showLogin = !currentUser && withLogin;

  return (
    <AuthContext.Provider value={{ currentUser, currentUserLoading, setCurrentUser, logout }}>
      {showLogin ? <Login /> : children}
    </AuthContext.Provider>
  );
};

export { AuthProvider };
