import { LOGGER_EVENT_LISTENER } from '@/constants';
import type { ILogMessageDetail } from '@/types';
import { LogLevel } from '@/types';

import { PortalMessage } from '../messaging';

const emitEvent = (detail: ILogMessageDetail) => {
  PortalMessage.emitMessageAsync(LOGGER_EVENT_LISTENER, detail);
};

export const LogInfo = (message: string, code: string, error?: Error, nameSpace?: string) =>
  emitEvent({ nameSpace, message, code, level: LogLevel.INFO, error });
export const LogDebug = (message: string, code: string, error?: Error, nameSpace?: string) =>
  emitEvent({ nameSpace, message, code, level: LogLevel.DEBUG, error });
export const LogWarn = (message: string, code: string, error?: Error, nameSpace?: string) =>
  emitEvent({ nameSpace, message, code, level: LogLevel.WARN, error });
export const LogError = (message: string, code: string, error?: Error, nameSpace?: string) =>
  emitEvent({ nameSpace, message, code, level: LogLevel.ERROR, error });
