import type { IIdentityService, ILoginPayloadWithCode } from '@/types';

import { KC_GRANT_TYPE } from '../config';
import { requestToken } from './tokenRequest';

export const loginWithCode = (payload: ILoginPayloadWithCode, identityService: IIdentityService) =>
  requestToken(identityService, payload.clientId, {
    grant_type: KC_GRANT_TYPE,
    code: payload.code,
    scope: payload.scope,
    redirect_uri: payload.redirectUri,
  });
