import type { AxiosResponse } from 'axios';

import { REQUEST_OPERATION_CLIENT_EVENT } from '@/constants';
import { LogError, PortalMessage } from '@/features';
import type { AxiosInstanceWithOperations, Consumer, OperationResult, Subscriber, Supplier } from '@/types';
import { OperationResponseStatus } from '@/types';

import fetchOperation from './adapters/fetchOperation';
import { OperationStatus } from './OperationStatus';

export interface IOperationClient {
  fetchOperationStatusAsync(operationId: string, apiVersion?: string): Promise<OperationResult>;
}

function* range(start: number, end: number, step: number) {
  while (start < end) {
    yield start;
    start += step;
  }
}

export const operationClient = async (): Promise<IOperationClient> => {
  return new Promise(resolve => {
    const consumer: Consumer<IOperationClient> = (operationClient: IOperationClient) => resolve(operationClient);
    PortalMessage.emitMessageAsync(REQUEST_OPERATION_CLIENT_EVENT, consumer);
  });
};

class OperationClient implements IOperationClient, Subscriber {
  private readonly url: string;

  constructor(operationUrl: string) {
    this.url = operationUrl;
  }

  public subscribe() {
    PortalMessage.subscribe(REQUEST_OPERATION_CLIENT_EVENT, (e: CustomEvent) => e.detail(this));
  }

  private handleRetry = (delays: number[], fetch: () => Promise<void>, resolve: (value: OperationResult) => void) => {
    const delay = delays.shift();
    if (delay == undefined) {
      resolve(OperationStatus.Error(new Error('retry count depleted')));
    } else {
      setTimeout(() => fetch(), delay);
    }
  };

  public fetchOperationStatusAsync = async (operationId: string, apiVersion = '1.0'): Promise<OperationResult> => {
    return new Promise<OperationResult>(resolve => {
      const delays = Array.from(range(1, 10, 1), (_, k) => Math.pow(2, k) * 100);

      const fetch = async () => {
        try {
          const operation = await fetchOperation(this.url, operationId, apiVersion);

          switch (operation.state) {
            case OperationResponseStatus.Completed:
              resolve(OperationStatus.Ok(operation));
              break;
            case OperationResponseStatus.InProgress:
              this.handleRetry(delays, fetch, resolve);
              break;
            default:
              resolve(OperationStatus.OperationRejected(operation));
              break;
          }
        } catch (error) {
          LogError('Error while fetching for operation status', 'error_fetching_operation', error as Error);
          this.handleRetry(delays, fetch, resolve);
        }
      };

      fetch();
    });
  };
}

const waitForOperation = async (
  axios: AxiosInstanceWithOperations,
  axiosAction: Supplier<Promise<AxiosResponse>>
): Promise<OperationResult> => {
  const response = await axiosAction();
  const operationId = response.headers['x-operation'];
  axios.pendingCreateOperationRequests -= 1;

  if (operationId == undefined) {
    throw Error('operation id not found');
  }

  axios.operationIds.push({ operationId, time: Date.now() });

  if (axios.pendingCreateOperationRequests === 0) {
    axios.emitter.emit('no-pending-create-operation-requests');
  }

  const client = await operationClient();

  return await client.fetchOperationStatusAsync(operationId);
};

export { OperationClient, waitForOperation };
