import { requestApiClientAsync } from '@/services';
import type { ILogMessage, ILogRemoteOptions } from '@/types';

export default async (messages: ILogMessage[], remoteOptions: ILogRemoteOptions) => {
  const axiosConfig = {
    headers: {
      'Content-Type': 'application/json;charset=UTF-8',
      'Access-Control-Allow-Origin': '*',
    },
    withCredentials: false,
  };

  axiosConfig.headers = { ...axiosConfig.headers, ...remoteOptions.headers };

  const api = await requestApiClientAsync();

  await api.post(remoteOptions.url, messages, axiosConfig);
};
