import type { AxiosInstance, AxiosResponse } from 'axios';

import type { ApiClientEmitter } from '@/services';

export enum OperationResponseStatus {
  Completed = 'completed',
  InProgress = 'inProgress',
  Rejected = 'rejected',
}

export interface IOperationResponse {
  id: string;
  userId: string;
  resourceId: string;
  name: string;
  state: OperationResponseStatus;
  resource: string;
  code: string;
  reason: string;
  payload: string;
}

export interface IRequestQueueItem {
  resolve: (value?: any) => void;
  reject: (reason?: any) => void;
  config: AxiosResponse['config'];
}

export enum OperationStatusCode {
  OK,
  ERROR,
  OperationRejected,
}

export interface OperationResult {
  statusCode: OperationStatusCode;
  error: Error | undefined;
  response: IOperationResponse | undefined;
  isError(): boolean;
}

export interface AxiosInstanceWithOperations extends AxiosInstance {
  operationIds: { operationId: string; time: number }[];
  pendingCreateOperationRequests: number;
  emitter: ApiClientEmitter;
}
