import type { Consumer, IPortalMessageSubscription } from '@/types';

export class PortalMessage {
  public static emitMessageAsync = <T>(eventName: string, message: T) => {
    Promise.resolve().then(() => {
      const event = new CustomEvent<T>(eventName, { detail: message });

      Promise.resolve().then(() => window.dispatchEvent(event));
    });
  };

  public static subscribe = (eventName: string, listener: Consumer<CustomEvent>): IPortalMessageSubscription => {
    window.addEventListener(eventName, listener as EventListener);

    return {
      unsubscribe: () => window.removeEventListener(eventName, listener as EventListener),
    };
  };
}
