import { LOGOUT_PATH } from '@/constants';
import { requestApiClientAsync } from '@/services';
import type { IIdentityService } from '@/types';

export async function logoutFromKeycloak(identityService: IIdentityService, tenantId: string, refreshToken: string) {
  const client = await requestApiClientAsync();
  const url = `${identityService.location}/${tenantId}${LOGOUT_PATH}`;
  const body = new URLSearchParams({ client_id: tenantId, refresh_token: refreshToken });

  await client.post(url, body, { headers: { 'Content-Type': 'application/x-www-form-urlencoded' } });
}
