import { REQUEST_API_CONFIGURATION_EVENT } from '@/constants';
import type { ConfigProps, Subscriber } from '@/types';

import { PortalMessage } from '../messaging';

export const requestApiConfigAsync = async (): Promise<ConfigProps> => {
  return new Promise(resolve => {
    PortalMessage.emitMessageAsync(REQUEST_API_CONFIGURATION_EVENT, (config: ConfigProps) => resolve(config));
  });
};

export class AppConfig implements Subscriber {
  public config: Partial<ConfigProps> = { apiUrl: '', domain: '', graphQLUrl: '', signalRUrl: '', hubLinkUrl: '' };

  constructor(config: Partial<ConfigProps>) {
    this.config = config;
  }

  subscribe() {
    PortalMessage.subscribe(REQUEST_API_CONFIGURATION_EVENT, (e: CustomEvent) => {
      e.detail(this.config);
    });
  }
}
