import type { IIdentityService, IRestTokenPayload } from '@/types';
import { AUTHENTICATION_TYPE } from '@/types';

import { requestToken } from './tokenRequest';

export const updateAccessToken = (payload: IRestTokenPayload, identityService: IIdentityService) =>
  requestToken(identityService, payload.client_id, {
    grant_type: AUTHENTICATION_TYPE.refreshToken,
    refresh_token: payload.refresh_token,
  });
