import { useEffect, useRef } from 'react';

import type { BridgeEvents, MessageHandler } from '@/types';

import { useMessageBus } from './useMessageBus';

export function useBusEvent<K extends keyof BridgeEvents>(event: K, handler: MessageHandler<BridgeEvents[K]>): void {
  const messageBus = useMessageBus();
  const handlerRef = useRef(handler);

  useEffect(() => {
    handlerRef.current = handler;
  }, [handler]);

  useEffect(() => {
    const unsubscribe = messageBus?.onEvent(event, msg => {
      handlerRef.current(msg);
    });

    return unsubscribe;
  }, [event, messageBus]);
}
