import { TOKEN_PATH } from '@/constants';
import { requestApiClientAsync } from '@/services';
import type { IIdentityService, IRestTokenResponse } from '@/types';

export type TokenRequestParams = Omit<Record<string, string>, 'client_id'> & {
  grant_type: string;
};

export async function requestToken(
  identityService: IIdentityService,
  realm: string,
  params: TokenRequestParams
): Promise<IRestTokenResponse> {
  const client = await requestApiClientAsync();
  const url = `${identityService.location}/${realm}${TOKEN_PATH}`;
  const body = new URLSearchParams({
    ...params,
    client_id: realm,
  });

  const response = await client.post<IRestTokenResponse>(url, body, {
    headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
  });

  return response.data;
}
