import type { IOperationResponse, OperationResult } from '@/types';
import { OperationStatusCode } from '@/types';

export class OperationStatus implements OperationResult {
  public static Ok(response: IOperationResponse): OperationStatus {
    return new OperationStatus(undefined, response, OperationStatusCode.OK);
  }

  public static Error(error: Error): OperationStatus {
    return new OperationStatus(error, undefined, OperationStatusCode.ERROR);
  }

  public static OperationRejected(response: IOperationResponse) {
    return new OperationStatus(undefined, response, OperationStatusCode.OperationRejected);
  }

  public readonly error: Error | undefined;
  public readonly response: IOperationResponse | undefined;
  public readonly statusCode: OperationStatusCode;

  constructor(error: Error | undefined, response: IOperationResponse | undefined, statusCode: OperationStatusCode) {
    this.error = error;
    this.response = response;
    this.statusCode = statusCode;
  }

  public isError = (): boolean => this.statusCode === OperationStatusCode.ERROR;
}
