import type { GenericObjectType, R2WCRenderer, WebComponentOptions } from '@/types';

import type { R2WCType } from './transforms';
import transforms from './transforms';

type PropName<Props> = Extract<keyof Props, string>;
type PropNames<Props> = Array<PropName<Props>>;

export function reactToWebComponentCore<Props, Context>(
  ReactComponent: React.ComponentType<Props>,
  options: WebComponentOptions<Props>,
  renderer: R2WCRenderer<Props, Context>
): CustomElementConstructor {
  if (!options.props) {
    options.props = ReactComponent.propTypes ? (Object.keys(ReactComponent.propTypes) as PropNames<Props>) : [];
  }

  const propNames = (
    Array.isArray(options.props) ? options.props.slice() : (Object.keys(options.props) as PropNames<Props>)
  ).filter(prop => {
    return prop !== 'container';
  });

  const propTypes = {} as Record<PropName<Props>, R2WCType>;
  const mapPropAttribute = {} as Record<PropName<Props>, string>;
  const mapAttributeProp = {} as Record<string, PropName<Props>>;
  for (const prop of propNames) {
    propTypes[prop] = Array.isArray(options.props) ? 'string' : options.props[prop];

    const attribute = toDashedStyle(prop);

    mapPropAttribute[prop] = attribute;
    mapAttributeProp[attribute] = prop;
  }

  class ReactWebComponent extends HTMLElement {
    static get observedAttributes() {
      return Object.keys(mapAttributeProp);
    }

    connected = true;
    context?: Context;
    props: Props = {} as Props;

    container: HTMLElement;

    constructor() {
      super();

      if (options.shadow) {
        this.container = this.attachShadow({
          mode: options.shadow,
        }) as unknown as HTMLElement;
      } else {
        this.container = this;
      }

      (this.props as GenericObjectType).container = this.container;

      for (const prop of propNames) {
        const attribute = mapPropAttribute[prop];
        const value = this.getAttribute(attribute);
        const type = propTypes[prop];
        const transform = transforms[type];

        if (value && transform?.parse) {
          (this.props as GenericObjectType)[prop] = transform.parse(value, this);
        }
      }
    }

    connectedCallback() {
      this.connected = true;
      this.render();
    }

    disconnectedCallback() {
      this.connected = false;
      if (this.context !== undefined) {
        const ctx = this.context;
        this.context = undefined;
        setTimeout(() => renderer.unmount(ctx));
      }
    }

    attributeChangedCallback(attribute: string, _: string, value: string) {
      const prop = mapAttributeProp[attribute];
      const type = propTypes[prop];
      const transform = transforms[type];

      if (prop in propTypes && transform?.parse) {
        (this.props as GenericObjectType)[prop] = transform.parse(value, this);

        this.render();
      }
    }

    render() {
      if (!this.connected) {
        return;
      }

      if (!this.context) {
        this.context = renderer.mount(this.container, ReactComponent, this.props);
      } else {
        renderer.update(this.context, this.props);
      }
    }
  }

  for (const prop of propNames) {
    const attribute = mapPropAttribute[prop];
    const type = propTypes[prop];

    Object.defineProperty(ReactWebComponent.prototype, prop, {
      enumerable: true,
      configurable: true,
      get() {
        return this.props[prop];
      },
      set(value: never) {
        this.props[prop] = value;

        const transform = transforms[type];
        if (transform?.stringify) {
          const attributeValue = transform.stringify(value);
          const oldAttributeValue = this.getAttribute(attribute);

          if (oldAttributeValue !== attributeValue) {
            this.setAttribute(attribute, attributeValue);
          }
        }
      },
    });
  }

  return ReactWebComponent;
}

function toDashedStyle(camelCase = '') {
  return camelCase.replace(/([a-z0-9])([A-Z])/g, '$1-$2').toLowerCase();
}
