import type { IRequestQueueItem } from '@/types';

export class RequestQueue {
  private queue: IRequestQueueItem[] = [];
  private tripWire = false;

  public enqueue = (item: IRequestQueueItem) => {
    this.queue.push(item);
  };

  public dequeue = () => {
    return this.queue.shift();
  };

  public get length() {
    return this.queue.length;
  }

  public get isTripWire() {
    return this.tripWire;
  }

  public set isTripWire(value: boolean) {
    this.tripWire = value;
  }
}
