import { doAxiosAsync } from '@/services';
import type { ContactDataPayload } from '@/types';

import { LogError } from '../logger';
import { requestApiConfigAsync } from '../subscribers';

const getContactsQuery = `
query GetContacts($tenantId: String!, $userId: String!) {
  contacts(tenantId: $tenantId, userIds: [$userId]) {
    id
    userId
    name
    avatarUrl
    avatarColor
    email
    details
    phone
    birthDate
    phoneNumbers
    address
    company
    socialMedia
    statusMessage
    additionalEmails
    publicColumns
    pathAlias
    coverImageUrl
    coverImageConfig
    user {
      id
    }
    positionId
    departmentId
    additionalAttributes
    department {
      id
      name
    }
    position {
      id
      jobTitle
    }
  }
}
`;

export const getContacts = async (tenantId: string, userId: string): Promise<ContactDataPayload[]> => {
  const { graphQLUrl } = await requestApiConfigAsync();
  const url = `${graphQLUrl}/graphql`;

  return await doAxiosAsync(
    async axios => await axios.post(url, { query: getContactsQuery, variables: { tenantId, userId } })
  )
    .then(result => result.data.data.contacts)
    .catch(error => {
      LogError('Error fetching contacts', 'error_fetching_contacts', error as Error);
      throw error;
    });
};
