import type { IOperationResponse } from '@/types';

import { requestApiClientAsync } from '../ApiClient';

export default async (url: string, operationId: string, apiVersion: string): Promise<IOperationResponse> => {
  const fetchUrl = `${url}/api/operations/${operationId}?api-version=${apiVersion}`;
  const apiClient = await requestApiClientAsync();
  const response = await apiClient.get(fetchUrl);

  return response.data;
};
