export class TableCache {
  private cache: {
    [key: string]: {
      data: any;
      timeout: any;
      cacheClearTime: number;
    };
  } = {};

  public get<T>(table: string, id: string): T | undefined {
    const cacheKey = this.getCacheKey(table, id);
    const cacheItem = this.cache[cacheKey];
    if (cacheItem) {
      return cacheItem.data;
    }

    return undefined;
  }

  public set<T>(table: string, id: string, data: T, cacheClearTime: number): void {
    const cacheKey = this.getCacheKey(table, id);
    const cacheItem = this.cache[cacheKey];
    if (cacheItem) {
      clearTimeout(cacheItem.timeout);
    }
    this.cache[cacheKey] = {
      data,
      timeout: setTimeout(() => {
        this.clearCacheKey(table, id);
      }, cacheClearTime),
      cacheClearTime,
    };
  }

  public clearCacheKey(table: string, id: string): void {
    const cacheKey = this.getCacheKey(table, id);
    const cacheItem = this.cache[cacheKey];
    if (cacheItem) {
      clearTimeout(cacheItem.timeout);
      delete this.cache[cacheKey];
    }
  }

  private getCacheKey(table: string, id: string): string {
    return `${table}_${id}`;
  }
}

const tableCache = new TableCache();
export { tableCache };
