import type { IIdentityService, ILoginPayload } from '@/types';

import { PASSWORD_GRANT_TYPE } from '../config';
import { requestToken } from './tokenRequest';

export const loginWithPassword = (payload: ILoginPayload, identityService: IIdentityService) =>
  requestToken(identityService, payload.clientId, {
    grant_type: PASSWORD_GRANT_TYPE,
    username: payload.userName,
    password: payload.password,
    scope: payload.scope,
  });
