import 'react-toastify/dist/ReactToastify.css';

import type { FC } from 'react';
import { useEffect } from 'react';
import type { Id, ToastContent, ToastOptions } from 'react-toastify';
import { toast, ToastContainer } from 'react-toastify';

import { REQUEST_REACT_TOAST } from '@/constants';
import { PortalMessage } from '@/features';
import type { BiOptionalFunction } from '@/types';

type ToastFunction<TModel> = BiOptionalFunction<ToastContent<TModel>, ToastOptions, Id>;

export async function requestToastAsync<T>(): Promise<ToastFunction<T>> {
  return new Promise(resolve => {
    const consumer = (t: ToastFunction<T>) => {
      resolve(t);
    };
    PortalMessage.emitMessageAsync(REQUEST_REACT_TOAST, consumer);
  });
}

export async function showMessage<TData>(content: ToastContent<TData>, options?: ToastOptions): Promise<Id> {
  const messenger = await requestToastAsync<TData>();

  return messenger(content, options);
}

const PicheToast: FC = () => {
  useEffect(() => {
    const subscription = PortalMessage.subscribe(REQUEST_REACT_TOAST, (e: CustomEvent) => e.detail(toast));

    return () => {
      subscription.unsubscribe();
    };
  }, []);

  return <ToastContainer />;
};

export { PicheToast };
